.p-header {
 height: rem(50);
 background: rgba($color: #fff, $alpha: 0.6);
 color: $text-color;
}

.p-header.change-color {
  background: #fff;
  box-shadow: 0 2px 2px $main-color;
  color: $text-color;

}

.p-header__inner {
  padding: 0 rem(20);
  height: inherit;

  @include mq(tab) {
    display: flex;
    padding-left: rem(33);
    padding-right: 0;
  }

}

.p-header__logo {
  width: rem(100);
  height: inherit;
  display: flex;
  align-items: center;


  @include mq(tab) {
    width: rem(150);
  
  }

}

.p-header__logo img {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.p-header__drawer {
  position: absolute;
  top: rem(17);
  right: rem(20);


}

.p-header__menu {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: scroll;
 

}

.p-header__overlay {
  width: 100%;
  height: 100vh;
  background: rgba($color: #222, $alpha: 0.6);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  display: none;
}

.p-header__overlay.open {
  display: block;
}



.p-header__pc-menu {
  margin-left: auto;
  height: inherit;
}