.p-mv-swiper {
   width: 100%;
   height: 100%;
   background: $main-color;
   .swiper-wrapper {
       .swiper-slide {
           overflow: hidden;
       }
       
       //Prevスライド
       .swiper-slide-prev,
       //Activeスライド
       .swiper-slide-active,
       //ループ時に複製される正面のスライド
       .swiper-slide-duplicate-active {

           img {
               animation-name: diagonalSlide;
               animation-duration: 1s;
               animation-fill-mode: forwards;
               animation-timing-function:linear;
              
           }
       }
   }
}

